@IonicPage()
@Component({
  selector: 'page-modal-map',
  templateUrl: 'modal-map.html',
})
export class ModalMapPage {
  coordenadas: any;
  coorInit: any;
  coorEnd: any;
  titleAtm: any;
  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController, public _ngZone: NgZone) {
  }
  map: GoogleMap;

  ionViewDidLoad() {
    console.log('ionViewDidLoad ModalMapPage');
    //
    this.coordenadas = this.navParams.get('coordenadas')
    this.coorInit = this.navParams.get('coorInit')
    this.coorEnd = this.navParams.get('coorEnd')
    this.titleAtm = this.navParams.get('titleAtm')
    console.log(this.coordenadas,this.coorInit,this.coorEnd)

  }
  ionViewDidEnter(){
    this.loadMap(this.coordenadas,this.coorInit,this.coorEnd,this.titleAtm)
  }
  dismiss() {
    this.viewCtrl.dismiss();
  }
loadMap(coorPolylines,coorInit,coorEnd,titleAtm){
   let latitude = coorInit.lat;
   let longitude = coorInit.lng;
   console.log(coorInit.lat,coorInit.lng)
//   this.myLatitudeStart = position.coords.latitude;
//   this.myLongitudeStart = position.coords.longitude;
   let mapOptions: GoogleMapOptions = {
     camera: {
       target: {
         lat: latitude,
         lng: longitude
       },
       zoom: 18,
       tilt: 30
    }
   };

  this.map = GoogleMaps.create('map_canvas2', mapOptions);
   console.log(this.map)
    let marker: Marker = this.map.addMarkerSync({
      title: 'My Position',
      icon: 'data:image/png;base64,',
      animation: 'DROP',
      position: {
       lat: latitude,
        lng: longitude
      }
    });
     this.map.addMarker({
      title: titleAtm,
       icon: 'data:image/png;base64,',
       animation: 'DR',
       position: {
         lat: coorPolylines[coorPolylines.length-1].lat,
         lng: coorPolylines[coorPolylines.length-1].lng
       }
     }).then((marker: Marker)=> {


      let htmlInfoWindow = new HtmlInfoWindow();
      var element: HTMLElement = document.createElement("H3")
      var text = document.createTextNode("IR");
      element.appendChild(text);
      // element.setAttribute("type", "button");
      // element.setAttribute("value", "ir");
      // element.setAttribute("name", "type");
      // console.log(element);
      function ir(){
      }
      element.addEventListener("click",(ViewController) =>{
        let viewCtrl: ViewController
        viewCtrl.dismiss();
      });
      // function ir(){
      //   alert('clicked');
      // }
      htmlInfoWindow.setContent(element);

      this._ngZone.run(() => {

        htmlInfoWindow.open(marker);
      });
      // var element = document.createElement("input");
      // //Assign different attributes to the element.
      // element.setAttribute("type", type);
      // element.setAttribute("value", type);
      // element.setAttribute("name", type);
      // element.setAttribute("onclick", alert("blabla"));



     })
    //  var lineSymbol = {
    //   path: this.map.CIRCLE,
    //   fillOpacity: 1,
    //   scale: 3
    //   };
     this.map.addPolyline({
             points:coorPolylines,
             color: "#3E9AFB",
             width: 10,
             geodesic:true,
             clickable: true,

     }).then((polilyne: Polyline) => {

      // polilyne.on(GoogleMapsEvent.POLYLINE_CLICK).subscribe((params: any)=>{
      //   // infowindow.setContent('<div><strong>' + place.name + '</strong><br>' +
      //   //         'Place ID: ' + place.place_id + '<br>' +
      //   //         place.formatted_address + '</div>');
      //   //       infowindow.open(map, this);

      // })
     })
//   // marker.on(GoogleMapsEvent.MARKER_CLICK).subscribe(() => {
//   //   alert('clicked');
//   // });
//   this.mapProvider.getLocationAtm(latitude,longitude, this)
//   // this.nearbyPlace(latitude,longitude)
  }

}



